/* Style for the Navbar Links */
.navbar-link {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.navbar-nav .nav-link {
    list-style: none;
    padding: 0 20px;
    text-decoration: none;
    font-size: 16px;
    font-weight: 600;
    color: #0a0a0a;
    transition: color 0.28s ease; /* Transition for text color */
    position: relative;
}

/* Hover and Active states */
.navbar-nav .nav-link:hover,
.navbar-nav .nav-link.active {
    color: #088178;
}

/* Underline effect on hover and active state */
.navbar-nav .nav-link:hover::after,
.navbar-nav .nav-link.active::after {
    content: " ";
    width: 50%;
    height: 3px;
    background: #088178;
    position: absolute;
    bottom: 1px;
    left: 15%;
    transition: width 0.28s ease; /* Transition for the width of the underline */
}
