body {
  font-family: 'Arial', sans-serif;
  line-height: 1.6;
  margin: 0;
  padding: 0;
  background-color: #FAFAFA;
  color: #34495e;
  height: 100%;
}

.App {
  height: 100%;
  width: 100%;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

header {
  background-color: #2c3e50;
  color: white;
  padding: 1rem 0;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
}

.nav-links {
  display: flex;
  gap: 20px;
}

.nav-links a {
  color: white;
  text-decoration: none;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #3498db;
}

.hero {
width: 50%;
  height: 500px;
  display: flex; 
  flex-direction: column;
  align-items: left;
  justify-content: center; 
  text-align: left;
  color: rgb(8, 8, 8);
  /* background-color: #2980b9; */
  
}
.hero1 {
  display: flex;
  align-items: center;
  justify-content: center;
}
.hero1 img{
  width: 500px;
  
}

.hero-content h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.cta-button {
  display: inline-block;
  background-color: #3498db;
  color: white;
  padding: 10px 20px;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.cta-button:hover {
  background-color: #2980b9;
}

.services {
  padding: 4rem 0;
}

.services h2 {
  text-align: center;
  margin-bottom: 2rem;
}

.service-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
}

.service-item {
  background-color: white;
  padding: 1.5rem;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.service-item:hover {
  transform: translateY(-5px);
}

.service-item h3 {
  color: #3498db;
  margin-top: 0;
}

/* From Uiverse.io by csemszepp */

#moving-text {

  overflow-x: auto;
  white-space: nowrap;
  /* background-color: #b3e5fd; */
  /* height: 300px; */
}

#moving-text::-webkit-scrollbar {
  display: none;
}

#moving-text .con {
  white-space: nowrap;
  display: inline-block;
  animation: scroll 20s linear;
  animation-iteration-count: infinite;
  


}

@keyframes scroll {
  from {
    transform: translateX(0%);
  }
  to {
    transform: translateX(-100%);
  }
}

#moving-text #con2 {
  white-space: nowrap;
  display: inline-block;
  animation: rscroll 20s linear;
  animation-iteration-count: infinite;
  


}


@keyframes rscroll {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0%);
  }
}

#moving-text .con h3 {
  white-space: nowrap;
  display: inline-block;  
  margin: 5px;
  font-size: 18px;
  border: 2px solid rgba(128, 128, 128, 0.29);
  padding: 10px 18px ;
  border-radius: 8px;
  white-space: nowrap;
  display: inline-block;
}

#moving-text .con h3 img{
  width: 50px;
}

